body{
    margin:0;
    background-color: #e5ffcc;
    font-family: sans-serif;
}
.todo-container{
    width: 350px;
    margin: auto;
    margin-top: 50px;
    text-align: center;
}
h1{
    margin: 0;
    margin-bottom: 15px;
    color:#5a793c;
}
.input-section{
    background-color: #ccff99;
    padding: 19px;
    border-radius: 10px;
    box-shadow: 0 7px 6px #0000003b;
}
.input-section input{
    border-radius: 5px;
    border:1px solid #0000003b;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    background-color: #e5ffcc;
    outline: none;
    color: #5a793c;
}
ul{
    text-align: left;
    padding: 0;
}
input::placeholder{
    color: #5a793c;
}
ul li{
    list-style: none;
    background-color: #ccff99;
    padding: 8px;
    margin-bottom: 12px;
    border-radius: 5px;
    color: #5a793c;
    box-shadow: 0 4px 6px #00000066;
    display: flex;

}
ul li i{
    text-align: right;
    flex:1;
    cursor: pointer;
}